import { title } from "process";
import React, { useState } from "react";
import { Radar } from "react-chartjs-2";
import './Formulaire.css';
import {Chart as ChartJS,RadialLinearScale,PointElement,LineElement,Filler,Tooltip,Legend,} from 'chart.js';

interface FormData {
  name: string;
  score: number;
}
interface FormTitle {
  title:string;
}



const Formulaire: React.FC = () => {
  
  ChartJS.register(RadialLinearScale,PointElement,LineElement,Tooltip,Legend);
  const reglage ={
      scales: {
          r: {
              angleLines: {display: true,color:'green'},
              grid:{display:true,color:'black'},
              suggestedMin: 0,
              suggestedMax: 10
          }
      }
  };
  const [formTitle,setformTitle]= useState<FormTitle>({title: "",});

  const [formDonnee, setformDonnee] = useState<FormData>({
    name: "",
    score: 0,
  });

  const [chartDonnee, setchartDonnee] = useState({
    labels: [formDonnee.name],
    datasets: [
      {
        label:formTitle.title,
        data: [formDonnee.score],
        backgroundColor: 'white',
        borderColor: 'DDDDDD',
        borderWidth: 3,
      },
    ],

  });

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setformDonnee({
      ...formDonnee,
      [event.target.name]:
        event.target.type === "number"
          ? (event.target.value)
          : event.target.value,
    });
  };

  const handleTitleChange=(event: React.ChangeEvent<HTMLInputElement>) => {setformTitle({...formTitle,[event.target.name]:event.target.value})};

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setchartDonnee({
      labels: [...chartDonnee.labels, formDonnee.name],
      datasets: [
        {
          ...chartDonnee.datasets[0],
          label: formTitle.title,
          data: [...chartDonnee.datasets[0].data, formDonnee.score],
        },
      ],
    });
  };

  return (
    <>
      <form className="selection" onSubmit={handleSubmit}>
      <div className="title">
          <label htmlFor="title">Titre :</label><br />
          <input type="text" id="title" name="title" value={formTitle.title} onChange={handleTitleChange}/>
        </div>
        <div className="name">
          <label htmlFor="name">Nom :</label><br />
          <input type="text" id="name" name="name" value={formDonnee.name} onChange={handleInputChange}/>
        </div>
        <div className="score">
          <label htmlFor="score">Score :</label><br />
          <input type="number" id="score" name="score" value={formDonnee.score} onChange={handleInputChange}/>
        </div>
        <br /><button className="submitForm" type="submit">Soumettre</button>
      </form>
      <div className="formulaire"><Radar data={chartDonnee} options={reglage}/></div>
    </>
  );
};

export default Formulaire;
